import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_REMOVE_ROW_HEADER = Symbol('TABLE_REMOVE_ROW_HEADER');

export function removeTableRowHeader(node: Table) {
  return {
    type: TABLE_REMOVE_ROW_HEADER,
    payload: {
      node,
    },
  };
}

interface TableRemoveTableRowHeaderPayload {
  node: Table;
}

interface TableRemoveTableRowHeaderAction {
  type: typeof TABLE_REMOVE_ROW_HEADER;
  payload: TableRemoveTableRowHeaderPayload;
}

export function isTableRemoveTableRowHeaderAction(
  action: Action,
): action is TableRemoveTableRowHeaderAction {
  return action.type === TABLE_REMOVE_ROW_HEADER;
}
